import React, { Component } from 'react';
import Content from '../../common/content';

class Yggl extends Component {
    // componentWillMount(){
    //     this.props.history.push(`${this.props.match.path}/dangangl`)
    // }
    render () {
        let {ygglList,routes} = this.props;
        return (
            <div className='yggl'>
                <Content contentData={ygglList} routes={routes}></Content>
            </div>
        )
    }
}

Yggl.defaultProps = {
    ygglList : [{
        link:'/dangangl',
        text:'档案管理',
        id:1
    },{
        link:'/fenpeigl',
        text:'分配管理',
        id:2
    },{
        link:'/kechenggl',
        text:'课程管理',
        id:3
    },{
        link:'/daikefeigl',
        text:'代课费管理',
        id:4
    },{
        link:'/birthday',
        text:'生日提醒',
        id:5
    },{
        link:'/xinchougl',
        text:'薪酬管理',
        id:6
    },{
        link:'/lizhigl',
        text:'离职管理',
        id:7
    }]
}

export default Yggl;