import React, { Component } from 'react'

export default class HeaderTable extends Component {
    constructor (props) {
        super(props)
        this.state = {
            index:0
        }
    }
    
    changeTab = (key) => {
        this.setState({
            index:key
        })
    }
    render() {
        let {tableData} = this.props;
        let {
            index
        } = this.state;
        return (
            <div className='header_table'>
                    <ul className='header_table_top'>
                        {
                            tableData.map((v,i) => {
                                return (
                                    <li key={i} onClick={() => {this.changeTab(i)}} className={index === i ? 'clickClass':''}>{v.text}</li>
                                )
                            })
                        }
                    </ul>
                    <table>
                        <thead style={{height:"30px"}}>
                            <tr>
                                <td>时间</td>
                                <td>课程/活动</td>
                                <td>圆所</td>
                                <td>班级</td>
                            </tr>
                        </thead>
                        <tbody>
                            {
                               tableData[index].child ?  tableData[index].child && tableData[index].child.map((v,i)=>{
                                    return <tr key={i}>
                                                <td>{v.time}</td>
                                                <td>{v.course}</td>
                                                <td>{v.school}</td>
                                                <td>{v.class}</td>
                                           </tr>
                                }) : '暂无数据'
                            } 
                        </tbody>
                    </table>
            </div>
        )
    }
}
HeaderTable.defaultProps = {
    tableData:[{
        text:'今日计划',
        child:[{
            time:'09:00-11:00',
            course:'体育课',
            school:'蓝天幼儿园',
            class:'大班-苹果班'
        },{
            time:'09:00-11:00',
            course:'体育课',
            school:'育才幼儿园',
            class:'大班-香蕉班'
        },{
            time:'09:00-11:00',
            course:'体育课',
            school:'小天使幼儿园',
            class:'大班-曼联班'
        },{
            time:'09:00-11:00',
            course:'体育课',
            school:'莱克幼儿园',
            class:'大班-月亮班'
        },{
            time:'09:00-11:00',
            course:'体育课',
            school:'哈佛幼儿园',
            class:'大班-太阳班'
        }]
    },{
        text:'本周计划'
    },{
        text:'综合统计'
    }]
}

