class Layout extends Component {
    constructor (props) {
        super(props);
        this.state = {};
    }
    componentDidMount(){
        window.tt = this;
        let {
            history,
            match:{
                path
            }
        } = this.props;
        history.push(`${path}/index`)
    }
    render() {
        let {
            routes
        } = this.props;
      return (
        <div className="App">
            <Header></Header>
            <section>
                <RenderRoute routes={routes}/>
            </section>
        </div>
      );
    }
}

export default Layout;
