import React, { Component } from 'react'

export default class User extends Component {
  render() {
      let {userlist} = this.props;
    return (
      <dl className='user'>
          <dt>
              <img src='/img/user.jpg' alt=''/>
          </dt>
          <dd>
              <h1>Hi,{userlist.name}</h1>
              <p>岗位 : {userlist.gangwei}</p>
              <p>总积分 : {userlist.allfen}</p>
              <p>积分排名 : {userlist.jfpm}</p>

          </dd> 
      </dl>
    )
  }
}
User.defaultProps ={
    userlist:{
        name:'Jim阳光',
        gangwei:'体能老师',
        allfen:'2000',
        jfpm:'100/2500'
    }
}
