import React, { Component } from 'react';
import {Badge,Icon} from 'antd';
import {NavLink,withRouter} from 'react-router-dom';
import 'antd/dist/antd.css';

class Header extends Component {
  constructor (props) {
    super(props)
    this.state={
      header_list:[{
        text:"首页",
        id:1,
        link:'/index'
      },{
        text:"员工管理",
        id:2,
        link:'/yggl'
      },{
        text:"员工考勤",
        id:3,
        link:'/ygkq'
      },{
        text:"工作计划",
        id:4,
        link:'/gzjh'
      },{
        text:"客户管理",
        id:5,
        link:'/khgl'
      },{
        text:"亚太学院",
        id:6,
        link:'/ytxy'
      },{
        text:"级别考核",
        id:7,
        link:'/jbkh'
      },{
        text:"管理助手",
        id:8,
        link:'/glzs'
      },{
        text:"积分管理",
        id:9,
        link:'/jfgl'
      }],
      userName:'jim阳光fafasfasgasfdas',
      index:0,
    }
  }
  navActive = (key) => {
      let {
        index
      } = this.state;
      this.setState({
        index:key
      })
  }
  render() {
    let {header_list,userName,index} = this.state;
    return (
      <header className='header'>
            <div className='logo'>
                <img src='/img/logo.jpg' alt=''/>
            </div>
            <div className='header_list'>
                <ul>
                    {
                      header_list && header_list.map((item,key) => {
                          return (
                              <NavLink to={this.props.match.path + item.link} key={item.id}><li onClick={ () => {this.navActive(key)}} className={index === key ? 'active' : ''}>{item.text}</li></NavLink>
                          )
                      })
                    }
                </ul>
            </div>
            <div className='userName'>
              <p>{userName}</p>
            </div>
            <div className='header_login'>
                <h3><Icon type="mail" theme="outlined" /><Badge count='3'></Badge><em>通知栏</em></h3>
            </div>
      </header>
    )
  }
}


export default withRouter(Header);