import React, { Component } from 'react';
import {withRouter,NavLink} from 'react-router-dom';

class Aside extends Component {
  constructor (props) {
    super(props)
    this.state = {
      index:0
    }
  }
  toContent = (i) => {
      let {index} = this.state;
      this.setState({
          index : i
      })
  }
  
  render() {
    let {asideData} = this.props;
    let {index} = this.state;
    return (
      <div className='content_aside'>
          <ul>
              {
                asideData && asideData.map((v,i)=>{
                  return (
                    <NavLink to={this.props.match.path+v.link} key={v.id}><li onClick={() =>{this.toContent(i)}} className={index===i?'aside_active':''}>{v.text}</li></NavLink>
                  )
                })
              }
          </ul>
      </div>
    )
  }
}

export default withRouter(Aside)


