import React, { Component } from 'react'

class Login extends Component {
    constructor (props) {
        super(props)
        this.state={
            btnClass:false
        }
    }
    //获取权限
    getPermissions = () => {
        if(localStorage.getItem('user') === 'pyz'){
            alert('已有权限,请登录！');
            return;
            
        }
        this.setState({
            btnClass:true
        },()=>{
           localStorage.setItem('user','pyz');
           alert('权限获取成功！'); 
        })
        
    }
    //登录
    toLogin = () => {
        if(localStorage.getItem('user') !== 'pyz'){
            alert('请获取权限！');
            this.setState({
                btnClass:false
            })
        }else{
            this.props.history.push('/layout/index')
        }
    }

    render () {
        localStorage.removeItem('user');
        let {btnClass} = this.state;
        return (
            <div className='login'>
                <div className='login_content'>
                    <h1>体智能管理系统</h1>
                    <form>
                        <input type='text' placeholder='请输入用户名'/><br/>
                        <input type='password' placeholder='请输入密码'/>
                    </form>
                    <button onClick={this.getPermissions} className={btnClass ? 'btnActive':''}>获取权限</button>
                    <button onClick={this.toLogin} className={btnClass ? '':'btnActive'}>登录</button>
                </div>
            </div>
        )
    }
}

export default Login