import React, { Component } from 'react';
import User from '../common/user';
import { Calendar } from 'antd';
import moment from 'moment';
import 'moment/locale/zh-cn';
import HeaderTable from '../common/header_table';
import {connect} from 'dva';
moment.locale('zh-cn');

class Index extends Component {
    
    render () {
        console.log(this.props)
        let {
            title,
            setTitle
        } = this.props;
        return (
            <div className='shouye'>
                <div className='shouye_left'>
                    <User/>
                </div>
                <div className='shouye_right'>
                <div><button onClick={() => {setTitle('柯榕最美')}}>{title}</button></div>
                    <div className='shouye_right_top'>
                        <div style={{ width: 300, border: '1px solid #d9d9d9', borderRadius: 4 }} className='shouye_data'>
                            <Calendar fullscreen={false}/>
                        </div>
                        <HeaderTable />
                    </div>
                </div>
            </div>
            );
    }
}
let mapState = (state) => {
    let {
        home
    } = state;
    console.log(home)
    return {...state.home}
}
let mapDispatch = (dispatch) => {
    return {
        setTitle(newTitle){
            dispatch({type:'home/setTitle',title:newTitle})
        }
    }
}

export default connect(mapState,mapDispatch)(Index);