import React, { Component } from 'react';
import Content from '../../common/content';

class Ygkq extends Component {
    render () {
        let {routes,ygkqData} = this.props;
        return (
            <div className='ygkq'>
                <Content contentData={ygkqData} routes={routes}></Content>
            </div>
        )
    }
}
Ygkq.defaultProps = {
    ygkqData:[
        {
            link:'/kqshenpi',
            text:'考勤审批',
            id:1
        },{
            link:'/kqtongji',
            text:'考勤统计',
            id:2
        },{
            link:'/kqmingxi',
            text:'考勤明细',
            id:3
        },{
            link:'/kqshezhi',
            text:'考勤设置',
            id:4
        }
    ]
}
export default Ygkq