import Login from '../components/login';
import Layout from '../App';
import Index from '../components/index';
import Yggl from '../components/yggl/index.js';
import Ygkq from '../components/ygkq/index.js';
import Gzjh from '../components/gzjh';
import Khgl from '../components/khgl';
import Ytxy from '../components/ytxy';
import Jbkh from '../components/jbkh';
import Glzs from '../components/glzs';
import Jfgl from '../components/jfgl';
import Dangangl from '../components/yggl/dangangl';
import Fenpeigl from '../components/yggl/fenpeigl';
import Kechenggl from '../components/yggl/kechenggl';
import Daikefeigl from '../components/yggl/daikefeigl';
import Dirthday from '../components/yggl/birthday';
import Xinchougl from '../components/yggl/xinchougl';
import Lizhigl from '../components/yggl/lizhigl';
import Kqmingxi from '../components/ygkq/kqmingxi';
import Kqshenpi from '../components/ygkq/kqshenpi';
import Kqshezhi from '../components/ygkq/kqshezhi';
import Kqtongji from '../components/ygkq/kqtongji';




export default [
    {
        path:'/login',
        component:Login
    },{
        path:'/layout',
        component:Layout,
        children:[{
            path:'/index',
            component:Index
        },{
            path:'/yggl',
            component:Yggl,
            children:[{
                path:'/dangangl',
                component:Dangangl
            },{
                path:'/fenpeigl',
                component:Fenpeigl
            },{
                path:'/kechenggl',
                component:Kechenggl
            },{
                path:'/daikefeigl',
                component:Daikefeigl
            },{
                path:'/birthday',
                component:Dirthday
            },{
                path:'/xinchougl',
                component:Xinchougl
            },{
                path:'/lizhigl',
                component:Lizhigl
            }]
        },{
            path:'/ygkq',
            component:Ygkq,
            children:[{
                path:'/kqmingxi',
                component:Kqmingxi
            },{
                path:'/kqshenpi',
                component:Kqshenpi
            },{
                path:'/kqshezhi',
                component:Kqshezhi
            },{
                path:'/kqtongji',
                component:Kqtongji
            }]
        },{
            path:'/gzjh',
            component:Gzjh
        },{
            path:'/khgl',
            component:Khgl
        },{
            path:'/ytxy',
            component:Ytxy
        },{
            path:'/jbkh',
            component:Jbkh
        },{
            path:'/glzs',
            component:Glzs
        },{
            path:'/jfgl',
            component:Jfgl
        }]
    }
]